<template lang="jade">
  .hello
    loader(v-show="!hands.length")
    .ui.massive.blue.button(v-if="id && !root.name && hands.length" @click="setMe()") 
    .ui.huge.buttons(v-if="!user")
      button.ui.blue.button(@click="loginFB")
        i.facebook.icon
        |  
      .or
      button.ui.orange.button(@click="loginGoogle")
        i.google.icon
        |  
    .ui.container(v-if="id && root.name && hands.length")
      form#main-form.ui.form.error.warning.success
        h2.ui.header 
        .sub.header
          | 
          i.red.star
          | 
        h4.ui.dividing.header 
        .field
          label.required 
          input(type='text' v-model='root.name', placeholder='')
        .field(v-bind:class="{error: root.address && (root.latlngColumn == 'undefined,undefined' || root.latlngColumn == '36.778261,-119.4179324')} ")
          label.required 
          input(v-model.trim='root.address', @keyup='checkLatLng(root.address)', placeholder='')

          span(v-show="root.address && root.latlngColumn !== 'undefined,undefined' && root.latlngColumn !== '36.778261,-119.4179324'")
            span(v-html='root.latlngColumn')
            a(:href="'https://www.google.com.tw/maps/search/' + root.address" target="_blank") &nbsp;&nbsp;

          .ui.error.message(v-show="(root.address && (root.latlngColumn == 'undefined,undefined' || root.latlngColumn == '36.778261,-119.4179324'))")
            .header 
            p 
              br
              | 
              a(href="https://www.google.com.tw/chrome/browser/desktop/" target="_blank") Chrome
              | 
          .ui.warning.message(v-show="usedAddr(root)")
            .header 
            p 

          .ui.warning.message(v-show="tooDetail(root.address)")
            .header 
            p 

        .ui.divider
        .field
          label 
          .two.fields
            .field 
              input(v-model='root.site', placeholder='')
              span(v-show='root.site')
                a(:href='root.site', target='_blank')
                  img(:src="'http://www.google.com/s2/favicons?domain=' + root.site" title='' alt='')
                  | 
            .field
              input(v-model='root.site2', placeholder='')
              span(v-show='root.site2')
                a(v-show='root.site2', :href='root.site2', target='_blank')
                  img(:src="'http://www.google.com/s2/favicons?domain=' + root.site2" title='' alt='')
                  | 

        .two.fields
          .field
            label 
            input(type='text', v-model='root.freetime', placeholder='')
          .field
            label.required 
            input(type='text' v-model='root.connect_me',  placeholder='e-mail')
        h4.ui.dividing.header 
        .field
          label 
          select.selectpicker(v-model='root.learner_role')
            // TODO : NG-OPTION
            option(value='')  --  -- 
            option(v-for="t in ['', '', '',' + ', ' + ', ' + ',' +  + ']", :selected='root.learner_role == t', :value="t") {{t}}
        .field
          label 
          select.selectpicker(v-model='root.learner_type')
            option(value='')  --  -- 
            option(v-for="t in ['','','','']", :selected='root.learner_type == t', :value="t") {{t}}
        .field(v-bind:class="{error:badAge(root.learner_birth), warning:!root.learner_birth}")

          label.required ()
          input(v-model='root.learner_birth', type='text', placeholder='1976')

        span(v-show='root.learner_birth') {{toAge(root.learner_birth)}}

        .ui.warning.message(v-show="!root.learner_birth")
          .header 
          p 1~2

        .ui.error.message(v-show="badAge(root.learner_birth)")
          .header 
          p 
           | ()

        .field
          label ()
          span
            input(v-model='root.child_birth', type='text', placeholder='()')
            br
            span(v-show='root.child_birth') {{toAge(root.child_birth)}}
            .ui.divider
            span(style="margin-left:15px;") 
              br
              input(v-model='root.child_birth2', type='text', placeholder='()')
              br
            span(v-show='root.child_birth2') {{toAge(root.child_birth2)}}~{{toAge(root.child_birth)}} 
          .ui.divider
          .three.fields
            .field
              label.required 
              input(v-model='root.learner_habit', placeholder='')
            .field
              label.required 
              input(v-model='root.share', placeholder='')
            .field
              label 
              input(v-model='root.ask', placeholder='?')
          .field
            label.required 
            textarea#note(v-model='root.note', rows='5', cols='30', placeholder='20')
          .ui.warning.message(v-show="root.note && root.note.length < 20")
            .header {{20 - root.note.length}}
            p         
          .ui.success.message(v-show="root.note && root.note.length >= 20")
            .header 
            p 

        .warning(v-show="!isValid(root)")
          | note: 
          i.red.star
          | 
      .ui.divider
      a.ui.large.blue.button(v-bind:class="{disabled: !isValid(root)}" @click="updateFlag")
        span(v-show='myIndex == -1') !!
        span(v-show='myIndex > -1') !!
</template>

<script>

import { handsRef, db } from '../firebase'
import mix from '../mixins/mix.js'
import Loader from './Loader'

export default {
  name: 'myflag',
  mixins: [mix],
  props: ['id', 'user', 'mySearch', 'provider', 'photoURL'],
  components: { Loader },
  data () {
    return {
      myIndex: -1,
      root: {},
      local: {}
    }
  },
  firebase: {
    hands: handsRef
  },
  methods: {
    setMe: function () {
      var l = this.hands.length
      console.log(l)
      if (!this.hands) {
        setTimeout(this.setMe, 2000)
        return
      }
      for (var i = 0; i < l; i++) {
        if (this.hands[i].id === this.id) {
          this.myIndex = i
          this.root = this.hands[i]
        }
      }
      if (this.id && this.myIndex === -1) {
        console.log('new')
        this.myIndex = l
        this.root = {
          name: this.user.providerData[0].displayName,
          id: this.id,
          photoURL: this.photoURL || '',
          note: ''
        }
      }
      console.log(this.root.name)
      console.log(this.root)
    },
    checkLatLng: function (add) {
      var vm = this
      console.log('checkLatLng:' + add)
      this.$http.get('https://api.opencagedata.com/geocode/v1/json?q=' + encodeURIComponent(add) + '&key=ee2340e9a9e146e090943337d14a76d4&language=zh&pretty=1').then(response => {
        var d = response.body
        console.log(d)
        var lat, lng
        try {
          lat = d.results[0].bounds.northeast.lat
          lng = d.results[0].bounds.northeast.lng
        } catch (e$) {
          console.log(e$)
        }
        if (!lat || !lng) {
          try {
            lat = d.query.results.Result.latitude
            lng = d.query.results.Result.longitude
          } catch (e$) {
            console.log(e$)
          }
        }
        vm.root.latlngColumn = lat + ',' + lng
        vm.local.lat = parseFloat(lat)
        vm.local.lng = parseFloat(lng)
      })
    },
    usedAddr: function (hand) {
      var ans, i$, ref$, len$, h
      ans = false
      for (i$ = 0, len$ = (ref$ = this.hands.filter(fn$)).length; i$ < len$; ++i$) {
        h = ref$[i$]
        if (h.latlngColumn === hand.latlngColumn) {
          ans = true
          break
        }
      }
      if (hand.latlngColumn === 'undefined,undefined' || hand.latlngColumn === '36.778261,-119.4179324') {
        ans = false
      }
      return ans
      function fn$ (o) {
        return o.name !== hand.name
      }
    },
    tooDetail: function (addr) {
      if (!addr) {
        return false
      }
      if (addr.match(/(||F|f)/)) {
        return true
      }
      return false
    },
    updateFlag: function () {
      this.root.lastUpdate = (new Date()).getTime()
      if (this.myIndex < this.hands.length) {
        db.ref('hands/' + this.myIndex).update(JSON.parse(JSON.stringify(this.root).replace('.key', 'key').replace('undefined', 'null')))
        alert('!')
      } else {
        console.log('new2')
        db.ref('hands/' + this.myIndex).set(JSON.parse(JSON.stringify(this.root).replace('.key', 'key').replace('undefined', 'null')))
        alert('!')
      }
    },
    loginFB: function () {
      this.$emit('loginFB')
    },
    loginGoogle: function () {
      this.$emit('loginGoogle')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

label.required::before {
  content: "*";
  color:red;
}

i.red.star::before{
  content: "*";
  color:red;    
}

</style>



