<template lang="jade">
  .hello
    h1.ui.header
      img(src="../assets/handshake0.png")
      | {{msg}}
      img(src="../assets/handshake0.png")

    h4.ui.header 
      span(v-if = "hands && hands.length") {{ hands.length }}
      span(v-else) 
      | 

    .ui.huge.buttons(v-if="!user")
      button.ui.blue.button(@click="loginFB")
        i.facebook.icon
        |  
      .or
      button.ui.orange.button(@click="loginGoogle")
        i.google.icon
        |  

    .ui.segment.container
      h2.ui.header 2.0?
      h4 2.0
      h2.ui.header ?
      h4 
        a(href="http://www.alearn.org.tw", target="_blank") 
        | 
      h2.ui.header ?
      h4 
      h2.ui.header ??
      h4 
      h2.ui.header ?
      h4 []
      h2.ui.header ?
      h4 
        a(href="mailto:bestian@gmail.com") e-mail
        | 
      h2.ui.header ?
      h4 :[]? 

</template>

<script>

import { handsRef } from '../firebase'
import mix from '../mixins/mix.js'
import Card from './Card'

export default {
  name: 'intro',
  components: { Card },
  props: ['id', 'user', 'mySearch', 'photoURL', 'book'],
  mixins: [mix],
  data () {
    return {
      msg: '2.0'
    }
  },
  firebase: {
    hands: handsRef
  },
  methods: {
    loginFB: function () {
      this.$emit('loginFB')
    },
    loginGoogle: function () {
      this.$emit('loginGoogle')
    },
    isFacebookApp: function () {
      var ua = navigator.userAgent || navigator.vendor || window.opera
      return (ua.indexOf('FBAN') > -1) || (ua.indexOf('FBAV') > -1)
    },
    locate: function (h) {
      this.$emit('locate', h)
    },
    addBook: function (id) {
      console.log(id)
      this.$emit('addBook', id)
    },
    removeBook: function (index) {
      console.log(index)
      this.$emit('removeBook', index)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.ui.avatar {
  max-height: 100px;
  max-width: 100px;
  border-radius: 50%;
}

</style>



